h1 {
  text-align: center;
}

a {
  color: rgb(0, 0, 0);
  font-size: 16px;
  font-weight: 400;
  opacity: 1 !important;
  padding: 0.7rem 20px;
}

/* mobile */
@media screen and (max-width: 600px) {
  .contact__heading {
    margin-top: 100px;
    font-size: xxx-large;
  }

  .contact {
    display: flex;
    justify-content: center;
  }

  .contact__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: rgba(0, 0, 0, 0.15) 0px 0.5rem 1rem;
    padding: 10px 0px;
    width: 360px;
    height: 207px;
  }

  .contact__email-icon {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--primary-color);
    width: 80px;
    height: 80px;
    border-radius: 50px;
  }

  .contact__email-title {
    font-size: x-large;
  }
}

